import * as THREE from "three";
import React, { Suspense, useRef, useMemo } from "react";
import {
  Canvas,
  extend,
  useThree,
  useLoader,
  useFrame,
} from "@react-three/fiber";
import { OrbitControls, Sky, Html } from "@react-three/drei";
import { Water } from "three-stdlib";
import "./App.css";

extend({ Water });

function Ocean() {
  const ref = useRef();
  const gl = useThree((state) => state.gl);
  const waterNormals = useLoader(THREE.TextureLoader, "/waternormals.jpeg");
  waterNormals.wrapS = waterNormals.wrapT = THREE.RepeatWrapping;
  const geom = useMemo(() => new THREE.PlaneGeometry(10000, 10000), []);
  const config = useMemo(
    () => ({
      textureWidth: 512,
      textureHeight: 512,
      waterNormals,
      sunDirection: new THREE.Vector3(),
      sunColor: 0xfff064,
      waterColor: 0x001e0f,
      distortionScale: 3.7,
      fog: false,
      format: gl.encoding,
    }),
    [gl.encoding, waterNormals]
  );
  useFrame(
    (state, delta) => (ref.current.material.uniforms.time.value += delta)
  );
  return <water ref={ref} args={[geom, config]} rotation-x={-Math.PI / 2} />;
}

function Box() {
  const ref = useRef();
  useFrame((state, delta) => {
    ref.current.position.y = 10 + Math.sin(state.clock.elapsedTime) * 10;
  });

  return (
    <mesh ref={ref} scale={0}>
      <boxGeometry></boxGeometry>
      <meshStandardMaterial />
      <Html>
        <div className="contents">
          <a
            href="https://www.linkedin.com/in/youngsu-han/"
            target="_blank"
            rel="noreferrer"
            className="threeD"
          >
            <div>FRONTEND DEVELOPER</div>
            <div>YEONGSU HAN</div>
          </a>
        </div>
      </Html>
    </mesh>
  );
}

export default function App() {
  return (
    <Canvas
      camera={{ position: [0, 5, 100], fov: 55, near: 1, far: 20000 }}
      className="container"
    >
      <pointLight position={[100, 100, 100]} />
      <pointLight position={[-100, -100, -100]} />
      <Suspense fallback={null}>
        <Ocean />
        <Box />
      </Suspense>
      <Sky scale={1500} sunPosition={[500, 150, -1000]} />
      <OrbitControls enableZoom={false} />
    </Canvas>
  );
}
